/*  src/pages/UsersTable.jsx  */
import React, { useEffect, useMemo, useState } from 'react';
import API from '../services/api';
import Sidebar   from '../components/AnalyticsSidebar';
import DataTable from '../components/DataTable';
import './_analytics.css';

const ALLOWED_ROLES = ['student','warden','gatekeeper','vc-office','guest-house','admin','superadmin'];
const HOSTEL_TYPES  = ['hostler','non-hostler'];

const digitsOnly = (v) => String(v || '').replace(/[^\d]/g, '');

export default function UsersTable() {
  const [allUsers, setAllUsers] = useState([]);
  const [loading, setLoading]   = useState(true);
  const [role, setRole]         = useState('all');            // filter: role
  const [status, setStatus]     = useState('all');            // filter: verified | unverified | all
  const [hostelFilter, setHostelFilter] = useState('all');    // filter: hostel type (students)
  const [refreshKey, setRefreshKey] = useState(0);

  /* ───────── fetch ───────── */
  useEffect(() => {
    (async () => {
      try {
        const { data } = await API.get('/users');
        setAllUsers(data || []);
      } catch (e) {
        alert(e.response?.data?.msg || e.message);
      } finally {
        setLoading(false);
      }
    })();
  }, [refreshKey]);

  /* ───────── derived rows after filters ───────── */
  const rows = useMemo(() => {
    return allUsers.filter(u => {
      if (role !== 'all'   && u.role !== role) return false;
      if (status !== 'all' && (status === 'verified' ? !u.verified : u.verified)) return false;
      if (hostelFilter !== 'all' && u.role === 'student' && u.hostelType !== hostelFilter) return false;
      return true;
    });
  }, [allUsers, role, status, hostelFilter]);

  /* ───────── actions ───────── */
  const verifyUser = async (id) => {
    if (!window.confirm('Mark this account as VERIFIED?')) return;
    try {
      await API.patch(`/users/${id}/verify`);
      setRefreshKey(k => k + 1);
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  const resetPassword = async (id) => {
    if (!window.confirm('Reset this user’s password? A temporary password will be emailed.')) return;
    try {
      await API.post(`/users/${id}/reset-password`);
      alert('Temporary password emailed to the user.');
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  const deleteUser = async (id) => {
    if (!window.confirm('Delete this user account? This cannot be undone.')) return;
    try {
      await API.delete(`/users/${id}`);
      setAllUsers(list => list.filter(u => u._id !== id));
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  /* ───────── add (single) ───────── */
  const [addOpen, setAddOpen] = useState(false);
  const [addForm, setAddForm] = useState({
    name: '', email: '', role: 'student', studentId: '',
    hostelType: 'hostler',
    guardian_contact: '',
    guardians_info: [{ name:'', relation:'', contact:'' }]
  });

  const addGuardianRow = () => {
    setAddForm(f => ({
      ...f,
      guardians_info: f.guardians_info.length >= 5 ? f.guardians_info : [...f.guardians_info, { name:'', relation:'', contact:'' }]
    }));
  };
  const removeGuardianRow = (idx) => {
    setAddForm(f => ({ ...f, guardians_info: f.guardians_info.filter((_,i)=>i!==idx) }));
  };

  const submitAdd = async () => {
    try {
      const payload = {
        name: addForm.name.trim(),
        email: addForm.email.trim(),
        role: addForm.role,
        studentId: addForm.role === 'student' ? addForm.studentId.trim() : undefined,
        hostelType: addForm.role === 'student' ? addForm.hostelType : undefined,
        guardian_contact: addForm.role === 'student' ? digitsOnly(addForm.guardian_contact) || undefined : undefined,
        guardians_info: (addForm.guardians_info||[])
          .map(g=>({ name:g.name?.trim(), relation:g.relation?.trim(), contact:digitsOnly(g.contact) }))
          .filter(g=>g.name || g.contact) // only non-empty rows
      };
      if (!payload.name || !payload.email || !ALLOWED_ROLES.includes(payload.role)) {
        alert('Please provide name, email and a valid role.');
        return;
      }
      if (payload.role === 'student') {
        if (!payload.studentId) return alert('Student ID is required for role "student".');
        if (!HOSTEL_TYPES.includes(payload.hostelType)) return alert('Select Hostler/Non-Hostler.');
      }
      await API.post('/users', payload); // server auto-verifies & emails temp password
      setAddOpen(false);
      setAddForm({
        name:'', email:'', role:'student', studentId:'',
        hostelType:'hostler',
        guardian_contact:'',
        guardians_info:[{name:'',relation:'',contact:''}]
      });
      setRefreshKey(k=>k+1);
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  /* ───────── edit (single) ───────── */
  const [edit, setEdit] = useState(null); // holds user object being edited

  const saveEdit = async () => {
    try {
      const payload = {
        name: edit.name,
        email: edit.email,
        role: edit.role,
        studentId: edit.role === 'student' ? (edit.studentId || '').trim() : undefined,
        hostelType: edit.role === 'student' ? edit.hostelType : undefined,
        guardian_contact: edit.role === 'student' ? (digitsOnly(edit.guardian_contact) || undefined) : undefined,
        guardians_info: (edit.guardians_info||[])
          .map(g=>({ name:(g.name||'').trim(), relation:(g.relation||'').trim(), contact:digitsOnly(g.contact) }))
          .filter(g=>g.name || g.contact),
      };
      await API.put(`/users/${edit._id}`, payload);
      setEdit(null);
      setRefreshKey(k => k + 1);
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  /* ───────── bulk upload (via CSV file) ───────── */
  const [bulkOpen, setBulkOpen] = useState(false);
  const [bulkFile, setBulkFile] = useState(null);
  const [bulkInfo, setBulkInfo] = useState({ rows: 0, valid: 0, invalid: 0, errors: [] });

  // Header contract (now includes hostelType + guardian_contact)
  const TEMPLATE_HEADERS = [
    'name','email','role','studentId','hostelType','guardian_contact',
    'guardian1_name','guardian1_relation','guardian1_contact',
    'guardian2_name','guardian2_relation','guardian2_contact',
    'guardian3_name','guardian3_relation','guardian3_contact',
    'guardian4_name','guardian4_relation','guardian4_contact',
    'guardian5_name','guardian5_relation','guardian5_contact'
  ];

  const SAMPLE_ROW = [
    'Ali Khan','ali.khan@example.com','student','21SW001','hostler','923001234567',
    'Abid Khan','Father','923001234567',
    'Nadia Khan','Mother','923451234567',
    'Hamza Khan','Brother','923331234567',
    'Sara Ahmed','Guardian','923211234567',
    '—','—',''
  ];

  const downloadTemplate = () => {
    const lines = [
      TEMPLATE_HEADERS.join(','),
      SAMPLE_ROW.join(','),
    ];
    const csv = lines.join('\n') + '\n';
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url; a.download = 'users_template.csv'; a.click();
    URL.revokeObjectURL(url);
  };

  // Robust CSV line splitter (supports quotes)
  const splitCSVLine = (line) => {
    const out = [];
    let cur = '';
    let inQuotes = false;
    for (let i = 0; i < line.length; i++) {
      const ch = line[i];
      if (ch === '"') {
        if (inQuotes && line[i + 1] === '"') { cur += '"'; i++; }
        else inQuotes = !inQuotes;
      } else if (ch === ',' && !inQuotes) {
        out.push(cur);
        cur = '';
      } else {
        cur += ch;
      }
    }
    out.push(cur);
    return out;
  };

  const parseCSV = (text) => {
    const lines = text.replace(/\r/g, '').split('\n').filter(l => l.trim().length > 0);
    if (!lines.length) return { headers: [], rows: [] };

    const headers = splitCSVLine(lines[0]).map(h => h.trim());
    const rows = lines.slice(1).map(line => splitCSVLine(line));

    return { headers, rows };
  };

  const validateHeaders = (headers) => {
    const missing = TEMPLATE_HEADERS.filter(h => !headers.includes(h));
    const extra   = headers.filter(h => !TEMPLATE_HEADERS.includes(h));
    return { missing, extra };
  };

  const handleFileChange = (e) => {
    const file = e.target.files?.[0];
    setBulkFile(file || null);
    setBulkInfo({ rows: 0, valid: 0, invalid: 0, errors: [] });
  };

  const loadAndParseCSV = (file) =>
    new Promise((resolve, reject) => {
      const fr = new FileReader();
      fr.onload = () => resolve(String(fr.result || ''));
      fr.onerror = reject;
      fr.readAsText(file, 'utf-8');
    });

  const submitBulk = async () => {
    try {
      if (!bulkFile) { alert('Please choose a CSV file.'); return; }
      const content = await loadAndParseCSV(bulkFile);
      const { headers, rows } = parseCSV(content);

      // Validate headers strictly
      const { missing, extra } = validateHeaders(headers);
      if (missing.length) {
        alert(`CSV is missing required columns:\n- ${missing.join('\n- ')}`);
        return;
      }
      if (extra.length) {
        if (!window.confirm(`CSV has unexpected columns:\n- ${extra.join('\n- ')}\n\nContinue and ignore them?`)) {
          return;
        }
      }

      const hIndex = Object.fromEntries(headers.map((h, i) => [h, i]));
      let valid = 0, invalid = 0;
      const errors = [];
      const users = rows.map((cols, rowIdx) => {
        const val = (key) => (cols[hIndex[key]] ?? '').trim();

        const name  = val('name');
        const email = val('email');
        const role  = val('role');
        const studentId = val('studentId');
        const hostelType = val('hostelType').toLowerCase();
        const guardian_contact = digitsOnly(val('guardian_contact'));

        const guardians = [];
        for (let i = 1; i <= 5; i++) {
          const gn = val(`guardian${i}_name`);
          const gr = val(`guardian${i}_relation`);
          const gc = digitsOnly(val(`guardian${i}_contact`));
          if (gn || gc) guardians.push({ name: gn, relation: gr, contact: gc });
        }

        const obj = {
          name, email, role,
          studentId: studentId || undefined,
          hostelType: role === 'student' ? hostelType : undefined,
          guardian_contact: role === 'student' ? (guardian_contact || undefined) : undefined,
          guardians_info: guardians
        };

        const roleOk = ALLOWED_ROLES.includes(role);
        const hostelOk = role !== 'student' || HOSTEL_TYPES.includes(hostelType);
        const studOk = role !== 'student' || !!studentId;

        const ok = !!name && !!email && roleOk && studOk && hostelOk;
        if (ok) valid++; else {
          invalid++;
          errors.push(`Row ${rowIdx + 2}: invalid fields (need name, email, valid role, studentId & hostelType for role "student")`);
        }
        return ok ? obj : null;
      }).filter(Boolean);

      setBulkInfo({ rows: rows.length, valid, invalid, errors });

      if (!users.length) {
        alert('No valid rows to upload.');
        return;
      }

      const { data } = await API.post('/users/bulk', { users });
      alert(`Created: ${data.created?.length || 0}\nSkipped: ${data.skipped?.length || 0}`);
      setBulkOpen(false);
      setBulkFile(null);
      setBulkInfo({ rows: 0, valid: 0, invalid: 0, errors: [] });
      setRefreshKey(k => k + 1);
    } catch (e) {
      alert(e.response?.data?.msg || e.message);
    }
  };

  /* table columns */
  const columns = [
    { header: 'Name',  cell: u => u.name },
    { header: 'Email', cell: u => u.email },
    { header: 'Role',  cell: u => u.role },
    { header: 'Student ID', cell: u => u.studentId || '—' },
    { header: 'Hostel Type', cell: u => (u.role === 'student' ? (u.hostelType || '—') : '—') },
    { header: 'Guardian Contact', cell: u => (u.role === 'student' ? (u.guardian_contact || '—') : '—') },
    { header: 'Guardians', cell: u => (u.guardians_info?.length ? `${u.guardians_info.length} saved` : '—') },
    { header: 'Verified', cell: u => (
        <span className={`status-badge ${u.verified ? 'status-approved' : 'status-pending'}`}>
          {u.verified ? 'Yes' : 'No'}
        </span>
      )
    },
    { header: 'Action', cell: u => (
        <div style={{display:'flex',gap:8,flexWrap:'wrap'}}>
          <button
            className="action-btn green"
            onClick={() => verifyUser(u._id)}
            disabled={u.verified}
            title={u.verified ? 'Already verified' : 'Mark verified'}
          >
            ✓ Verify
          </button>
          <button className="action-btn" onClick={() => resetPassword(u._id)}>↻ Reset PW</button>
          <button className="action-btn blue" onClick={() => setEdit(u)}>✎ Edit</button>
          <button className="action-btn red"  onClick={() => deleteUser(u._id)}>🗑 Delete</button>
        </div>
      )
    },
  ];

  return (
    <div className="split-layout">
      <Sidebar />

      <div className="table-wrapper">
        <div className="table-header-row">
          <h2>👥 Users</h2>
          <div className="btn-row">
            <button className="action-btn" onClick={downloadTemplate}>⇩ CSV Template</button>
            <button className="action-btn" onClick={()=>setBulkOpen(true)}>⬆ Bulk Upload</button>
            <button className="action-btn blue" onClick={()=>setAddOpen(true)}>＋ Add User</button>
          </div>
        </div>

        {/* Filters row (role + status + hostel type) */}
        <div className="search-filter" style={{gap:12}}>
          <div>
            <label style={{fontSize:12,display:'block',opacity:0.7}}>Role</label>
            <select value={role} onChange={e=>setRole(e.target.value)}>
              <option value="all">All</option>
              {ALLOWED_ROLES.map(r=> <option key={r} value={r}>{r}</option>)}
            </select>
          </div>

          <div>
            <label style={{fontSize:12,display:'block',opacity:0.7}}>Status</label>
            <select value={status} onChange={e=>setStatus(e.target.value)}>
              <option value="all">All</option>
              <option value="verified">Verified</option>
              <option value="unverified">Unverified</option>
            </select>
          </div>

          <div>
            <label style={{fontSize:12,display:'block',opacity:0.7}}>Hostel Type</label>
            <select value={hostelFilter} onChange={e=>setHostelFilter(e.target.value)}>
              <option value="all">All</option>
              {HOSTEL_TYPES.map(t => <option key={t} value={t}>{t}</option>)}
            </select>
          </div>
        </div>

        {/* DataTable has built-in free-text search; include hostelType too */}
        <DataTable
          columns={columns}
          rows={rows}
          searchKeys={[ 'name', 'email', 'studentId', 'hostelType' ]}
        />

        {/* Add modal */}
        {addOpen && (
          <div className="modal-overlay">
            <div className="modal-card">
              <h3>Add User</h3>
              <p style={{marginTop:4,opacity:.7}}>Users added by admin are auto-verified and emailed a temporary password.</p>
              <div className="form-grid">
                <label>
                  <span>Name</span>
                  <input value={addForm.name} onChange={e=>setAddForm({ ...addForm, name:e.target.value })} />
                </label>
                <label>
                  <span>Email</span>
                  <input type="email" value={addForm.email}
                         onChange={e=>setAddForm({ ...addForm, email:e.target.value })} />
                </label>
                <label>
                  <span>Role</span>
                  <select value={addForm.role} onChange={e=>setAddForm({ ...addForm, role:e.target.value })}>
                    {ALLOWED_ROLES.map(r=> <option key={r} value={r}>{r}</option>)}
                  </select>
                </label>

                {addForm.role === 'student' && (
                  <>
                    <label>
                      <span>Student ID</span>
                      <input value={addForm.studentId}
                             onChange={e=>setAddForm({ ...addForm, studentId:e.target.value })} />
                    </label>
                    <label>
                      <span>Hostel Type</span>
                      <select value={addForm.hostelType}
                              onChange={e=>setAddForm({ ...addForm, hostelType:e.target.value })}>
                        {HOSTEL_TYPES.map(t => <option key={t} value={t}>{t}</option>)}
                      </select>
                    </label>
                    <label>
                      <span>Primary Guardian Contact (SMS)</span>
                      <input placeholder="923001234567" value={addForm.guardian_contact}
                             onChange={e=>setAddForm({ ...addForm, guardian_contact: digitsOnly(e.target.value) })} />
                    </label>
                  </>
                )}
              </div>

              <h4 style={{marginTop:10}}>Guardians (up to 5)</h4>
              {addForm.guardians_info.map((g,idx)=> (
                <div key={idx} style={{display:'grid',gridTemplateColumns:'1fr 1fr 1fr auto',gap:8,marginTop:6}}>
                  <input placeholder="Name" value={g.name}
                         onChange={e=>{
                           const gi=[...addForm.guardians_info]; gi[idx]={...gi[idx], name:e.target.value};
                           setAddForm({...addForm, guardians_info:gi});
                         }} />
                  <input placeholder="Relation" value={g.relation}
                         onChange={e=>{
                           const gi=[...addForm.guardians_info]; gi[idx]={...gi[idx], relation:e.target.value};
                           setAddForm({...addForm, guardians_info:gi});
                         }} />
                  <input placeholder="Contact (923…)" value={g.contact}
                         onChange={e=>{
                           const gi=[...addForm.guardians_info]; gi[idx]={...gi[idx], contact:digitsOnly(e.target.value)};
                           setAddForm({...addForm, guardians_info:gi});
                         }} />
                  <button className="action-btn red" onClick={()=>removeGuardianRow(idx)}>✕</button>
                </div>
              ))}
              <div className="modal-actions" style={{justifyContent:'flex-start'}}>
                <button className="action-btn" onClick={addGuardianRow} disabled={addForm.guardians_info.length>=5}>＋ Add Guardian</button>
              </div>

              <div className="modal-actions">
                <button className="action-btn" onClick={()=>setAddOpen(false)}>Cancel</button>
                <button className="action-btn blue" onClick={submitAdd}>Save</button>
              </div>
            </div>
          </div>
        )}

        {/* Edit modal */}
        {edit && (
          <div className="modal-overlay">
            <div className="modal-card">
              <h3>Edit User</h3>
              <div className="form-grid">
                <label>
                  <span>Name</span>
                  <input value={edit.name} onChange={e=>setEdit({ ...edit, name:e.target.value })} />
                </label>
                <label>
                  <span>Email</span>
                  <input type="email" value={edit.email}
                         onChange={e=>setEdit({ ...edit, email:e.target.value })} />
                </label>
                <label>
                  <span>Role</span>
                  <select value={edit.role} onChange={e=>setEdit({ ...edit, role:e.target.value })}>
                    {ALLOWED_ROLES.map(r=> <option key={r} value={r}>{r}</option>)}
                  </select>
                </label>

                {edit.role === 'student' && (
                  <>
                    <label>
                      <span>Student ID</span>
                      <input value={edit.studentId || ''}
                             onChange={e=>setEdit({ ...edit, studentId:e.target.value })} />
                    </label>
                    <label>
                      <span>Hostel Type</span>
                      <select value={edit.hostelType || 'hostler'}
                              onChange={e=>setEdit({ ...edit, hostelType:e.target.value })}>
                        {HOSTEL_TYPES.map(t => <option key={t} value={t}>{t}</option>)}
                      </select>
                    </label>
                    <label>
                      <span>Primary Guardian Contact (SMS)</span>
                      <input placeholder="923001234567" value={edit.guardian_contact || ''}
                             onChange={e=>setEdit({ ...edit, guardian_contact: digitsOnly(e.target.value) })} />
                    </label>
                  </>
                )}
              </div>

              <h4 style={{marginTop:10}}>Guardians</h4>
              {(edit.guardians_info||[{name:'',relation:'',contact:''}]).map((g,idx)=> (
                <div key={idx} style={{display:'grid',gridTemplateColumns:'1fr 1fr 1fr auto',gap:8,marginTop:6}}>
                  <input placeholder="Name" value={g.name||''}
                         onChange={e=>{
                           const gi=[...(edit.guardians_info||[])]; gi[idx]={...gi[idx], name:e.target.value};
                           setEdit({...edit, guardians_info:gi});
                         }} />
                  <input placeholder="Relation" value={g.relation||''}
                         onChange={e=>{
                           const gi=[...(edit.guardians_info||[])]; gi[idx]={...gi[idx], relation:e.target.value};
                           setEdit({...edit, guardians_info:gi});
                         }} />
                  <input placeholder="Contact (923…)" value={g.contact||''}
                         onChange={e=>{
                           const gi=[...(edit.guardians_info||[])]; gi[idx]={...gi[idx], contact:digitsOnly(e.target.value)};
                           setEdit({...edit, guardians_info:gi});
                         }} />
                  <button className="action-btn red" onClick={()=>{
                    const gi=[...(edit.guardians_info||[])]; gi.splice(idx,1); setEdit({...edit, guardians_info:gi});
                  }}>✕</button>
                </div>
              ))}
              <div className="modal-actions" style={{justifyContent:'flex-start'}}>
                <button className="action-btn" onClick={()=>{
                  const gi=[...(edit.guardians_info||[])];
                  if (gi.length<5) gi.push({name:'',relation:'',contact:''});
                  setEdit({...edit, guardians_info:gi});
                }} disabled={(edit.guardians_info?.length||0)>=5}>＋ Add Guardian</button>
              </div>

              <div className="modal-actions">
                <button className="action-btn" onClick={()=>setEdit(null)}>Cancel</button>
                <button className="action-btn blue" onClick={saveEdit}>Save</button>
              </div>
            </div>
          </div>
        )}

        {/* Bulk modal (file upload) */}
        {bulkOpen && (
          <div className="modal-overlay">
            <div className="modal-card" style={{maxWidth:'900px', width:'min(90vw,900px)'}}>
              <h3>Bulk Upload Users</h3>
              <p style={{opacity:.8, margin:'6px 0 12px'}}>
                Upload a CSV file using the exact template headers. For <b>students</b>, <code>studentId</code> and
                <code> hostelType</code> are required. Up to five guardians per student via <code>guardianN_*</code> columns.
                Primary SMS number goes in <code>guardian_contact</code> (digits only, e.g. 923001234567).
              </p>

              <div style={{display:'flex', alignItems:'center', gap:8, flexWrap:'wrap', marginBottom:10}}>
                <input type="file" accept=".csv,text/csv" onChange={handleFileChange} />
                <button className="action-btn" onClick={downloadTemplate}>⇩ Download Template (with sample row)</button>
              </div>

              {bulkFile && (
                <div style={{background:'#f8fafc', border:'1px solid #e5e7eb', borderRadius:8, padding:10, marginBottom:10}}>
                  <div><b>Selected:</b> {bulkFile.name}</div>
                  {bulkInfo.rows > 0 && (
                    <div style={{marginTop:6}}>
                      <div>Total rows: {bulkInfo.rows}</div>
                      <div>Valid: {bulkInfo.valid} &nbsp; Invalid: {bulkInfo.invalid}</div>
                      {bulkInfo.errors.length > 0 && (
                        <details style={{marginTop:6}}>
                          <summary>Show row errors</summary>
                          <ul style={{marginTop:6}}>
                            {bulkInfo.errors.map((e,i)=> <li key={i} style={{fontFamily:'monospace'}}>{e}</li>)}
                          </ul>
                        </details>
                      )}
                    </div>
                  )}
                </div>
              )}

              <div className="modal-actions">
                <button className="action-btn" onClick={()=>{ setBulkOpen(false); setBulkFile(null); setBulkInfo({rows:0,valid:0,invalid:0,errors:[]}); }}>Cancel</button>
                <button className="action-btn blue" onClick={submitBulk}>Upload</button>
              </div>
            </div>
          </div>
        )}

        {/* tiny CSS for modal (scoped) */}
        <style>{`
          .table-header-row{display:flex;align-items:center;justify-content:space-between;gap:12px;margin-bottom:8px}
          .btn-row{display:flex;gap:8px;flex-wrap:wrap}
          .modal-overlay{position:fixed;inset:0;background:rgba(0,0,0,.25);display:flex;align-items:center;justify-content:center;z-index:50}
          .modal-card{background:#fff;border-radius:10px;padding:20px;min-width:520px;max-width:90vw;box-shadow:0 8px 24px rgba(0,0,0,.15)}
          .form-grid{display:grid;grid-template-columns:1fr 1fr;gap:12px;margin:12px 0}
          .form-grid label span{display:block;font-size:12px;opacity:.7;margin-bottom:4px}
          .form-grid input,.form-grid select{width:100%;padding:8px;border:1px solid #e5e7eb;border-radius:6px}
          .modal-actions{display:flex;justify-content:flex-end;gap:8px;margin-top:12px}
          .action-btn{padding:6px 12px;border:none;border-radius:6px;background:#e5e7eb;cursor:pointer}
          .action-btn.blue{background:#2563eb;color:#fff}
          .action-btn.red{background:#dc2626;color:#fff}
          .action-btn.green{background:#059669;color:#fff}
          .action-btn:disabled{opacity:.6;cursor:not-allowed}
        `}</style>

        {loading && <p style={{opacity:.7}}>Loading…</p>}
      </div>
    </div>
  );
}
